  /* pre */
  
  /* table */
}
.falcon-app {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: absolute;
}
.falcon-app pre {
  margin-top: 0;
  margin-bottom: 20px;
  padding: 10px 20px;
  line-height: 24px;
  background: #eee;
  border: solid 1px #dddee1;
  box-sizing: border-box;
}
.falcon-app code {
  font-family: Menlo, Monaco, Consolas, Courier, monospace;
  box-sizing: border-box;
}
.falcon-app blockquote {
  margin: 0;
  padding: 5px 20px;
  margin-bottom: 20px;
  border-left: 5px solid #dddee1;
}
.falcon-app hr {
  height: 0;
  border: 3px dashed #dddee1;
}
.falcon-app p {
  padding: 0;
  margin: 0;
}
/* doc container */
.falcon-doc {
  position: relative;
  height: 100%;
  overflow: hidden;
}
.falcon-doc__header {
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-flex-direction: column;
          -ms-flex-direction: column;
              flex-direction: column;
      height: 100%;
}
.falcon-doc__header__brand {
        height: 61px;
        line-height: 61px;
        padding: 0 20px;
        border-right: solid 1px #e6e6e6;
}
.falcon-doc__header__brand--name {
          font-size: 2rem;
          font-weight: bolder;
}
.falcon-doc__header__brand--version {
          font-size: 1rem;
}
.falcon-doc__header__nav {
  position: relative;
  height: 100%;
  overflow: auto;
        -webkit-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
.falcon-doc__content {
  position: relative;
  height: 100%;
  overflow: auto;
      padding: 0 20px;
}
/* table */
.falcon-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}
.falcon-table td, .falcon-table th {
  border: 1px solid #dddee1;
  padding: 5px 10px;
  text-align: left;
}
.falcon-table th {
  background: #f1f4f9;
  color: #5c6b77;
  font-weight: 600;
  white-space: nowrap;
}
/* demo */
.falcon-demo {
    position: relative;
    overflow: auto;
    margin-bottom: 20px;

    /* 代码运行结果块 */

    /* 功能控制块 */
}
.falcon-demo pre {
  /* 代码高亮 */
  display: none;
  margin-bottom: 0 !important;
}
.falcon-demo__live {
      position: relative;
      overflow: auto;
      border: 1px solid #dddee1;
      padding: 10px 20px;
}
.falcon-demo__control {
      padding: 10px;
      border: 1px solid #dddee1;
}
.falcon-demo--showcode > pre {
      display: block;
}
.falcon-demo--allhidden {
      display: none;
}
.falcon-demo--livedark > .falcon-demo__live {
      background: #222;
      color: #fff;
}
.falcon-demo--livefull > .falcon-demo__live {
      padding: 0;
}
.falcon-demo--livenoborder > .falcon-demo__live {
      border: 0;
}
.falcon-demo--codenoborder > pre {
      border: 0;
}



